<template>
  <PageBody>
    <div class="list-container">
      <div class="query-body">
        <router-link tag="button" class="add-btn" to='/wall/detail'>添加教练</router-link>
      </div>
      <div class="list-body" ref="imgList">
        <PictureList :pictures="listData" v-loading="isLoading">
          <div slot-scope="{picture}" class="picture-box">
            <img :src="picture.image" alt="" class="picture-img">
            <div class="picture-buttons top-buttons">
              <router-link tag="button" :to="'/wall/detail/'+picture.id">
                <i class="iconfont gym-icon-edit"></i>
              </router-link>
              <button @click="handleDelete(picture)">
                <i class="iconfont gym-icon-trash"></i>
              </button>
            </div>
            <div class="picture-buttons bottom-buttons">
              <button @click="handleZoom(picture)">
                <i class="iconfont gym-icon-zoom"></i>
              </button>
              <button @click="handleMore(picture)">
                <i class="iconfont gym-icon-more"></i>
              </button>
            </div>
          </div>
        </PictureList>
      </div>
      <div class="paging-body">
        <Page
          :total="this.page.total"
          :current="this.page.pageNum"
          :page-size="this.page.pageSize"
          show-elevator show-total :transfer="false"
          @on-change="pageNumChange"
        />
      </div>
      <PictureDetail :visible="picture" @close="picture = false"/>
      <CoachDetail :visible="coach" @close="coach = false"/>
    </div>
  </PageBody>
</template>
<script>
import PictureList from 'views/components/PictureList'
import PictureDetail from './components/PictureDetail'
import CoachDetail from './components/CoachDetail'
import { getData, deleteDataById } from 'api/wall'
import list from '@/mixins/list'

export default {
  components: {PictureList, PictureDetail, CoachDetail},
  mixins: [list],
  data () {
    return {
      query: {},
      isLoading: false,
      listData: [],
      picture: false,
      coach: false,
      page: {
        pageNum: 1,
        pageSize: 12,
        total: 0
      }
    }
  },
  methods: {
    getList (data = {}) {
      this.isLoading = true
      let query = {
        pageSize: 12,
        pageNum: 1
      }
      this.query = Object.assign({}, query, data)
      getData(this.query).then(res => {
        if (Number(res.code) === 200) {
          this.listData = res.data
          this.page.pageNum = Number(res.pageNum)
          this.page.total = Number(res.total)
        } else {
          this.listData = []
          this.$message({
            message: res.message,
            type: 'warning'
          })
        }
      }).finally(() => {
        this.isLoading = false
      })
    },
    pageNumChange (pageNum) {
      this.listQuery({
        pageNum: String(pageNum)
      })
    },
    handleDelete (picture) {
      this.$confirm({
        title: '教练墙',
        brief: '确定删除此教练吗？',
        confirm: (dialog) => {
          dialog.loading = true
          deleteDataById({id: picture.id}).then(res => {
            if (Number(res.code) === 200) {
              dialog.destroy()
              this.listRefresh()
            } else {
              dialog.loading = false
              this.$message({
                message: res.message,
                type: 'warning'
              })
            }
          })
        }
      })
    },
    handleZoom (picture) {
      this.picture = picture
    },
    handleMore (picture) {
      this.coach = picture
    }
  }
}
</script>
<style lang='stylus' scoped>
  .list-container
    width 100%
    height 100%
    display flex
    flex-direction column
    align-items stretch
  .query-body
    padding 0 20px
    height 64px
    display flex
    flex-direction row
    align-items center
    justify-content flex-end
    border-bottom 1px solid #EAEAEA
  .paging-body
    border-top 1px solid #EAEAEA
    height 60px
    width 100%
    padding-right 20px
    display: flex
    justify-content flex-end
    align-items center
  .add-btn
    background #E1EAF8
    color #6494DD
    width 108px
    height 32px
    border-radius 4px
  .list-body
    flex 1
    padding 20px 20px 0
    overflow auto
  .picture-box
    width 130px
    height 156px
    background #F4F4F4
    display flex
    position relative
  .picture-img
    flex 1
  .picture-buttons
    position absolute
    left 0
    padding 0 6px
    width 100%
    display flex
    flex-direction row
    justify-content space-between
    &.top-buttons
      top 6px
    &.bottom-buttons
      bottom 6px
    & > button
      background rgba(237, 237, 237, .4)
      color #524D4D
      width 20px
      height 20px
      border-radius 4px
    & .iconfont
      position relative
      top -2px
  .picture-describe
    padding 0 4px
    position absolute
    bottom 0
    width 100%
    height 24px
    line-height 24px
    background rgba(237, 237, 237, .4)
    color #2B3242
  .picture-buttons
  .picture-describe
    opacity 0
    transition opacity 0.2s
  .picture-box:hover .picture-buttons
  .picture-box:hover .picture-describe
    opacity 1
</style>



